import classNames from 'classnames';
import { Carousel } from 'react-bootstrap';
import img1 from '../assets/mainPageImgs/mainImg1.jpg';
import img2 from '../assets/mainPageImgs/mainImg2.jpg';
import img3 from '../assets/mainPageImgs/mainImg3.jpg';
import img4 from '../assets/mainPageImgs/mainImg4.jpg';
import img5 from '../assets/mainPageImgs/mainImg5.jpg';
import img6 from '../assets/mainPageImgs/mainImg6.jpg';
import img7 from '../assets/mainPageImgs/mainImg7.jpg';
import img8 from '../assets/mainPageImgs/mainImg8.jpg';
import img9 from '../assets/mainPageImgs/mainImg9.jpg';
import FooterComponent from '../components/FooterComponent';
import styles from '../css-modules/mainPage.module.css';

const Main = () => {
  let bgImgComb = classNames('d-block', styles.bgImg);

  return (
    // TODO    
    <div className={styles.main}>
      <Carousel indicators={false}>
        <Carousel.Item interval={3000}>
          <img className={bgImgComb} src={img1} alt=" " />
        </Carousel.Item>
        <Carousel.Item interval={3000}>
          <img className={bgImgComb} src={img2} alt=" " />
        </Carousel.Item>
        <Carousel.Item interval={3000}>
          <img className={bgImgComb} src={img3} alt=" " />
        </Carousel.Item>
        <Carousel.Item interval={3000}>
          <img className={bgImgComb} src={img4} alt=" " />
        </Carousel.Item>
        <Carousel.Item interval={3000}>
          <img className={bgImgComb} src={img5} alt=" " />
        </Carousel.Item>
        <Carousel.Item interval={3000}>
          <img className={bgImgComb} src={img6} alt=" " />
        </Carousel.Item>
        <Carousel.Item interval={3000}>
          <img className={bgImgComb} src={img7} alt=" " />
        </Carousel.Item>
        <Carousel.Item interval={3000}>
          <img className={bgImgComb} src={img8} alt=" " />
        </Carousel.Item>
        <Carousel.Item interval={3000}>
          <img className={bgImgComb} src={img9} alt=" " />
        </Carousel.Item>
      </Carousel>
      <div className={styles.mainHeader}>
        <h1></h1>
        <h2>  </h2>
      </div>
      <FooterComponent />
    </div>
  );
};

export default Main;
